import Tollywoodarray from "./tollywoodarray";
import Bigprototype from "../bigprototype1";
import Smallprototype from "../smallprototype1";
import Advertisement from "../advertisement";
import "../tourism/tourism.css";

function Tollywood() {
  return (
    <section classname="tourism" style={{ backgroundColor: " #e9ddd4" }}>
      <div className="tourismdiv1">
        <h2>Tollywood</h2>
        <div> {Tollywoodarray.map(Bigprototype)} </div>
      </div>

      <div className="tourismdiv2">
        <h3 className="tourismh3">Top Posts </h3>

        <div> {Tollywoodarray.map(Smallprototype)} </div>
        <Advertisement />
      </div>
    </section>
  );
}

export default Tollywood;
