import React from "react";
import "./bigproptotype1.css";

function Bigprototype1(lis) {
  return (
    <div className="bigprototype">
      <div
        className="backgroundimage"
        style={{ backgroundImage: `url(${lis.image})` }}
      ></div>

      <div className="bigdata">
        <h3 className="bigdatah3">{lis.title}</h3>
        <p className="bigdatap">{lis.data}</p>

        <p className="p2">{lis.category} / 26 May 2022</p>
      </div>
    </div>
  );
}

export default Bigprototype1;
