import Fit1 from "./fitness1.webp";
import Fit2 from "./fitness2.jpeg";
import Fit3 from "./fitness3.jpg";
import Fit4 from "./fitness4.jpg";
import Fit5 from "./fitness5.jpg";
import Bigprototype from "../bigprototype1";
import Smallprototype from "../smallprototype1";
import Advertisement from "../advertisement";
import "../tourism/tourism.css";

const fitarray = [
  {
    key: 0,
    title:
      "Smart Health: I Tried the Rolflex Pro for Sore Muscles — and It Helped Ease Painful Spots",
    image: Fit1,
    category: "Fitness",
    data:
      "Avoiding workout burnout with rest may be good for you, but here’s what you should know about how taking time off can impact your performance."
  },
  {
    key: 1,
    title: "How Fast Do You Actually Lose Fitness?",
    image: Fit2,
    category: "Fitness",
    data:
      "The Rolflex Pro may be a more portable alternative to traditional foam rollers."
  },
  {
    key: 2,
    title:
      "The Rolflex Pro may be a more portable alternative to traditional foam rollers.",
    image: Fit3,
    category: "Fitness",
    data:
      "You may be able to optimize and support your fitness performance with supplements, but not all are worthwhile, and some (in large doses) may be dangerous."
  },
  {
    key: 3,
    title:
      "How to Get Started With Walking Workouts: An Absolute Beginner’s Guide",
    image: Fit4,
    category: "Fitness",
    data:
      "Picking the right pace, wearing the right shoes, and knowing how long to go at it can help you get the most out of a walking workout."
  },
  {
    key: 4,
    title: "How to Know When You Need More Calories for Your Workouts",
    image: Fit5,
    category: "Fitness",
    data:
      "Most casual exercisers don’t need lots of extra food to fuel their workouts. But if you’re experiencing certain symptoms, it might be time to add more snacks to your eating plan."
  }
];

function Fitness() {
  return (
    <section classname="tourism" style={{ backgroundColor: " #e9ddd4" }}>
      <div className="tourismdiv1">
        <h2>Fitness</h2>
        <div> {fitarray.map(Bigprototype)} </div>
      </div>

      <div className="tourismdiv2">
        <h3 className="tourismh3">Top Posts </h3>

        <div> {fitarray.map(Smallprototype)} </div>
        <Advertisement />
      </div>
    </section>
  );
}

export default Fitness;
