import React from "react";
import "./home.css";
import Image1 from "./images/nature_1.jpg";
import Image2 from "./images/nature2.jpg";
import Image3 from "./images/nature3.png";

function Div1() {
  return (
    <div className="div1home">
      <p className="bigphoto1" style={{ backgroundImage: `url(${Image2})` }}>
        <h2>Nature itself is the best physician</h2>

        <span className="span1"> Travel/22 may 2022</span>
      </p>

      <div className="smallphoto">
        <p
          className="smallphoto1"
          style={{ backgroundImage: `url(${Image1})` }}
        ></p>
        <p
          className="smallphoto1"
          style={{ backgroundImage: `url(${Image3})` }}
        ></p>
      </div>
    </div>
  );
}

export default Div1;
