import React from "react";
import "./smallprototype.css";

function Smallprototype(lis) {
  return (
    <section className="smallprototype">
      <div
        className="backgroundimg"
        style={{ backgroundImage: `url(${lis.image})` }}
      ></div>

      <div className="smalldata">
        <h3 className="smalldatah3">{lis.title}</h3>
        <footer>{lis.category} / 26 May 2022</footer>
      </div>
    </section>
  );
}

export default Smallprototype;
