import React from "react";
import { Link } from "react-router-dom";
import "./navigation.css";

function Nav() {
  return (
    <div className="navigation">
      <p className="navp">
        <span className="span">The</span>Siren
      </p>

      <ul className="lists">
        <Link to="/" style={{ textDecoration: "none", color: "rgb(12, 8, 8)" }}>
          <li>Home</li>
        </Link>
        <Link
          to="/tourism"
          style={{ textDecoration: "none", color: "rgb(12, 8, 8)" }}
        >
          <li>Tourism</li>
        </Link>

        <Link
          to="/tollywood"
          style={{ textDecoration: "none", color: "rgb(12, 8, 8)" }}
        >
          <li>Bollywood</li>
        </Link>

        <Link
          to="/technology"
          style={{ textDecoration: "none", color: "rgb(12, 8, 8)" }}
        >
          <li>Technology</li>
        </Link>

        <Link
          to="/food"
          style={{ textDecoration: "none", color: "rgb(12, 8, 8)" }}
        >
          <li>Food</li>
        </Link>

        <Link
          to="/fitness"
          style={{ textDecoration: "none", color: "rgb(12, 8, 8)" }}
        >
          <li>Fitness</li>
        </Link>
      </ul>
    </div>
  );
}

export default Nav;
